"use client"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { mockJobOrders } from "@/lib/mock-data"
import { ArrowRight } from "lucide-react"
import Link from "next/link"
import { Checkbox } from "@/components/ui/checkbox"
import { useState } from "react"
import { Badge } from "@/components/ui/badge"

interface CourseTabsProps {
  userName: string
}

export function CourseTabs({ userName }: CourseTabsProps) {
  const [jobOrders, setJobOrders] = useState(mockJobOrders)
  const [selectedTasks, setSelectedTasks] = useState<Set<string>>(new Set())

  const userOrders = jobOrders.filter((order) => order.assignedTo === userName)
  const inProgress = userOrders.filter((order) => order.status === "in-progress")
  const allNewTasks = jobOrders.filter((order) => order.status === "pending")
  const completed = userOrders.filter((order) => order.status === "completed")

  const handleTaskSelection = (orderId: string, checked: boolean) => {
    setSelectedTasks((prev) => {
      const newSet = new Set(prev)
      if (checked) {
        newSet.add(orderId)
      } else {
        newSet.delete(orderId)
      }
      return newSet
    })

    if (checked) {
      setJobOrders((prevOrders) =>
        prevOrders.map((order) =>
          order.id === orderId ? { ...order, status: "in-progress" as const, assignedTo: userName } : order,
        ),
      )
    }
  }

  const getIconColor = (index: number) => {
    const colors = [
      "bg-yellow-400/20 text-yellow-400",
      "bg-blue-400/20 text-blue-400",
      "bg-pink-400/20 text-pink-400",
      "bg-emerald-400/20 text-emerald-400",
    ]
    return colors[index % colors.length]
  }

  const getIconEmoji = (index: number) => {
    const emojis = ["💡", "🎨", "🎬", "📚"]
    return emojis[index % emojis.length]
  }

  const getPriorityColor = (priority: string) => {
    switch (priority) {
      case "high":
        return "bg-red-500/10 text-red-500 border-red-500/20"
      case "medium":
        return "bg-yellow-500/10 text-yellow-500 border-yellow-500/20"
      case "low":
        return "bg-green-500/10 text-green-500 border-green-500/20"
      default:
        return "bg-muted text-muted-foreground"
    }
  }

  const renderJobCard = (order: (typeof mockJobOrders)[0], index: number, isNewTask = false) => {
    const dueDate = new Date(order.dueDate).toLocaleDateString("en-US", {
      day: "numeric",
      month: "short",
      year: "2-digit",
    })

    return (
      <div key={order.id} className="p-5 rounded-xl bg-card border border-border/50 hover:shadow-md transition-all">
        <div className="space-y-4">
          {isNewTask && (
            <div className="flex items-center gap-3 pb-3 border-b border-border/50">
              <Checkbox
                id={`task-${order.id}`}
                checked={selectedTasks.has(order.id)}
                onCheckedChange={(checked) => handleTaskSelection(order.id, checked as boolean)}
                className="data-[state=checked]:bg-primary data-[state=checked]:border-primary"
              />
              <label htmlFor={`task-${order.id}`} className="text-sm font-medium text-foreground cursor-pointer flex-1">
                Select this task
              </label>
            </div>
          )}

          <div className="flex items-start justify-between">
            <div className={`w-14 h-14 rounded-full ${getIconColor(index)} flex items-center justify-center text-2xl`}>
              {getIconEmoji(index)}
            </div>
            <Badge variant="outline" className={getPriorityColor(order.priority)}>
              {order.priority}
            </Badge>
          </div>

          <div>
            <h3 className="font-semibold text-foreground text-base">{order.deviceType}</h3>
            <p className="text-xs text-muted-foreground mt-1">{order.jobNumber}</p>
            {isNewTask && <p className="text-xs text-muted-foreground mt-1">Quantity: {order.quantity} units</p>}
            {order.notes && <p className="text-xs text-muted-foreground mt-2 italic">{order.notes}</p>}
          </div>

          <div className="flex items-center justify-between text-xs text-muted-foreground">
            <span className="text-[10px] uppercase tracking-wide">Deadline</span>
            <span className="font-medium">{dueDate}</span>
          </div>
        </div>
      </div>
    )
  }

  return (
    <div className="space-y-4">
      <Tabs defaultValue="in-progress" className="w-full">
        <div className="flex items-center justify-between mb-6">
          <TabsList className="bg-transparent border-b border-border rounded-none h-auto p-0">
            <TabsTrigger
              value="in-progress"
              className="rounded-none border-b-2 border-transparent data-[state=active]:border-foreground data-[state=active]:bg-transparent data-[state=active]:shadow-none font-semibold"
            >
              In progress ({inProgress.length})
            </TabsTrigger>
            <TabsTrigger
              value="new-tasks"
              className="rounded-none border-b-2 border-transparent data-[state=active]:border-foreground data-[state=active]:bg-transparent data-[state=active]:shadow-none text-muted-foreground data-[state=active]:text-foreground"
            >
              New Tasks ({allNewTasks.length})
            </TabsTrigger>
            <TabsTrigger
              value="completed"
              className="rounded-none border-b-2 border-transparent data-[state=active]:border-foreground data-[state=active]:bg-transparent data-[state=active]:shadow-none text-muted-foreground data-[state=active]:text-foreground"
            >
              Completed ({completed.length})
            </TabsTrigger>
          </TabsList>
          <Link
            href="/technician-tasks"
            className="text-sm text-muted-foreground hover:text-foreground flex items-center gap-1 font-medium transition-colors"
          >
            More <ArrowRight className="h-4 w-4" />
          </Link>
        </div>

        <TabsContent value="in-progress" className="mt-0">
          {inProgress.length === 0 ? (
            <p className="text-sm text-muted-foreground text-center py-8">No jobs in progress</p>
          ) : (
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              {inProgress.map((order, index) => renderJobCard(order, index, false))}
            </div>
          )}
        </TabsContent>

        <TabsContent value="new-tasks" className="mt-0">
          {allNewTasks.length === 0 ? (
            <p className="text-sm text-muted-foreground text-center py-8">No new tasks available</p>
          ) : (
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              {allNewTasks.map((order, index) => renderJobCard(order, index, true))}
            </div>
          )}
        </TabsContent>

        <TabsContent value="completed" className="mt-0">
          {completed.length === 0 ? (
            <p className="text-sm text-muted-foreground text-center py-8">No completed jobs</p>
          ) : (
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              {completed.map((order, index) => renderJobCard(order, index, false))}
            </div>
          )}
        </TabsContent>
      </Tabs>
    </div>
  )
}
